
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

.moblie_body {
  position: fixed;
  width: 100%;
  height: 100%;
  background: black;
  z-index: 1;
}
.dialog_content {
  width: 100%;
  height: 100px;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  position: absolute;
  text-align: center;
  color: white;
  font-size: 14px;
}
.mobile_img {
  width: 90px;
}

html {
  background-image: linear-gradient(45deg, #e3e0e0a1, transparent);
}
.images_body {
  padding: 2rem;
}
.carousel-3d-slide {
  -webkit-box-shadow: -1px 15px 20px 0px;
          box-shadow: -1px 15px 20px 0px;
  border-color: #00000005;
}
.carousel-3d-controls a span {
  color: #3333334a;
}

@-webkit-keyframes rotate {
0% {
    -webkit-transform: perspective(400px) rotateZ(20deg) rotateX(-40deg) rotateY(0);
            transform: perspective(400px) rotateZ(20deg) rotateX(-40deg) rotateY(0);
}
100% {
    -webkit-transform: perspective(400px) rotateZ(20deg) rotateX(-40deg) rotateY(-360deg);
            transform: perspective(400px) rotateZ(20deg) rotateX(-40deg) rotateY(-360deg);
}
}
@keyframes rotate {
0% {
    -webkit-transform: perspective(400px) rotateZ(20deg) rotateX(-40deg) rotateY(0);
            transform: perspective(400px) rotateZ(20deg) rotateX(-40deg) rotateY(0);
}
100% {
    -webkit-transform: perspective(400px) rotateZ(20deg) rotateX(-40deg) rotateY(-360deg);
            transform: perspective(400px) rotateZ(20deg) rotateX(-40deg) rotateY(-360deg);
}
}
.stars {
  -webkit-transform: perspective(500px);
          transform: perspective(500px);
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  position: absolute;
  -webkit-perspective-origin: 50% 100%;
          perspective-origin: 50% 100%;
  left: 50%;
  -webkit-animation: rotate 90s infinite linear;
          animation: rotate 90s infinite linear;
  bottom: 0;
}
.star {
  width: 2px;
  height: 2px;
  background: #f7f7b8;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.login_body {
  width: 100%;
  height: 100%;
}
.login_content {
  width: 280px;
  height: 100px;
  background: #ffffff3b;
  border-radius: 15px;
  margin: auto;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  -webkit-box-shadow: 5px 5px 40px 1px;
          box-shadow: 5px 5px 40px 1px;
  padding: .4rem 1.6rem 1.5rem 1.6rem;
}
.input_area_content {
  width: 75%;
  background: #ffffff7a;
  height: 24px;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
  border-radius: 4px;
}
.input_content {
  height: 22px;
  width: 100%;
  padding-left: 2px;
  -webkit-transform: translate(-2px, -2px);
          transform: translate(-2px, -2px);
  background: transparent;
  font-weight: 700;
  padding: .1rem .4rem;
  font-size: 15px;
}
.error_info {
    margin: 0;
    font-size: 14px;
    color: #ff00008c;
    padding: .1rem 0;
}
